import firebase from 'firebase'
  
  export const initializeRepo = () => {

	  // Initialize Firebase
	  var config = {
		apiKey: "AIzaSyAWf5t7AAd3K0wmPh-ComsQbd_IK7XXYRU",
		authDomain: "hypotheke-9098d.firebaseapp.com",
		databaseURL: "https://hypotheke-9098d.firebaseio.com",
		storageBucket: "hypotheke-9098d.appspot.com",
		messagingSenderId: "390353629321"
	  };
	  firebase.initializeApp(config);
  }
  
  export function addHypo(h) {
	  let dtKey = new Date().toJSON().replace(".","-")
	  firebase.database().ref('hypos/' + dtKey).set(h);
  }


